 	&__font-tel,
	&__font-mail {
		font-weight: 500;
		font-size: 12px;
		line-height: 1.17;
		letter-spacing: 0.02em;

		@media screen and (min-width: 1200px) {
			font-size: 14px;
			line-height: 1.14;
		}
	}

	&__link {
		display: flex;
		align-items: center;

		color: var(--color-main);
	}

	&__link:hover,
	&__link:focus {
		color: var(--color-accent);
	}

	&__icon {
		display: none;
		margin-right: 10px;
		fill: currentColor;

		@media screen and (min-width: 768px) {
			display: inline-block;
		}
	}

	&__icon:hover {
		color: var(--color-accent);
	}
}
